.NxtPay{display:flex;flex-direction:column;align-items:center}.NxtPay .overdue{color:#d52b1e;font-family:'Bolt_demi';font-size:2em;padding:40px  0 10px}.NxtPay .btnGroup{margin-bottom:20px}.NxtPay .btnGroup button:after{border-radius:4px}.NxtPay .btnGroup button:first-child{border-radius:50px 0 0 50px}.NxtPay .btnGroup button:nth-of-type(2){border-radius:0 50px 50px 0;margin-left:2px;padding:10px 20px}.NxtPay .frequency{font-size:1.2em}.NxtPay .period{color:#babec5;font-size:.8em;display:block;padding:10px 0}
