.FPSHeader{background:#393A3D;color:#fff;position:relative;padding-top:50px}.FPSHeader .close{position:absolute;right:0;top:0}.FPSHeader .titleSub{display:flex;padding:0 20px;align-items:baseline}.FPSHeader .titleSub h3,.FPSHeader .titleSub h4{margin:0}.FPSHeader .titleSub h3{font-size:1.6em;font-family:'Bolt_rg';font-weight:300;margin-right:10px}.FPSHeader .titleSub h4{font-size:1.2em;font-family:'Bolt_rg';font-weight:300;opacity:.7}.FPSHeader .status{width:100%;padding:20px 0}.FPSHeader .status th,.FPSHeader .status td{padding:2px 10px}.FPSHeader .status th{font-family:'Bolt_rg';font-weight:300;text-align:left;text-transform:uppercase;opacity:.7;font-size:.85em}.FPSHeader .status td{font-size:1.2em}.FPSHeader .status em{font-style:normal;background:#d0021c;padding:2px 3px}
