.Software section{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:134px 134px;grid-gap:15px}.Software section div{cursor:pointer;background:#fff;box-shadow:0 4px 10px rgba(0,0,0,0.1);border-radius:8px;display:flex;justify-content:center;align-items:center;flex-direction:column;padding:15px;box-sizing:border-box;text-align:center;border:1px solid rgba(255,255,255,0);transition:.4s ease;user-select:none}.Software section div:hover{color:#30a048}.Software section div .hi{color:#30a048;font-size:3em;position:relative}.Software section div .hi:before{position:relative;z-index:1}.Software section div .hi:after{content:"";display:block;position:absolute;z-index:0;width:20px;height:20px;left:20%;top:20%;background:#fff}.Software section div.selected{border:1px solid #30a048;background:rgba(48,160,72,0.2);box-shadow:0 2px 5px rgba(0,0,0,0.1)}.Software section div.selected:hover{color:#333}.Software section div span{margin-top:10px}.Software .blueLink{display:block;padding:30px 0 10px}
