.ReviewDrawerFields{flex:1;display:flex;flex-direction:column;justify-content:space-between}.ReviewDrawerFields h4{font-family:'Bolt_rg';font-weight:300;text-transform:uppercase;opacity:.6;font-size:.9em;margin:35px 10px 10px}.ReviewDrawerFields>section{flex:1;max-height:calc(100vh - 315px);overflow-y:scroll;position:relative}.ReviewDrawerFields>section .blueLinks{padding:20px 0;background:#fff;position:absolute;bottom:0;left:0;width:100%}.ReviewDrawerFields>section .blueLink{border:none;background:none;padding:5px 10px}
