.Employee{display:flex;justify-content:space-between;align-items:center;background:#fff;padding:20px;margin-top:15px;border:1px solid #eeeff2}.Employee label:not(.Switch){opacity:.6;font-size:.8em}.Employee .calculate{margin-right:20px}.Employee .calculate article{align-items:flex-end}.Employee .calculate article:nth-of-type(2){align-items:flex-start;text-align:right;margin:0 40px}.Employee .controls{flex:1;display:flex;align-items:center}.Employee .controls .enableEdit{opacity:1;transition:.2s ease}.Employee .controls .enableEdit.disabled{opacity:.4}.Employee .controls>section{flex:1;display:flex;align-items:center;justify-content:space-between}.Employee .controls>section>div{display:flex;align-items:center}.Employee .controls>section>div article{display:flex;flex-direction:column}.Employee .EditPayrun{background:none;border:none}.Employee .EditPayrun .hi{font-size:1.2em;color:#0077c5}
