.NextSteps{background:#fff;padding:40px 40px 20px 40px;border-radius:4px;box-shadow:0 5px 20px rgba(0,0,0,0.07);margin:20px 0 80px}.NextSteps h3{margin:0 0 40px 0;opacity:.5;text-transform:uppercase}.NextSteps h4{margin:0 0 10px 0}.NextSteps article{display:flex;justify-content:space-between;margin:15px 0;padding:15px 0;border-top:1px solid #eee;cursor:pointer}.NextSteps article:hover:before{border:2px solid #30a048}.NextSteps article:before{content:"";display:block;border:2px solid rgba(0,0,0,0.2);width:25px;height:25px;border-radius:50%;margin-right:20px;transition:.1s ease}.NextSteps article:nth-of-type(1){margin-top:0;padding-top:0;border:0}.NextSteps article>div{flex:2}.NextSteps article aside{flex:1.5;display:flex;justify-content:flex-end;align-items:center}
