.OperationsDetail{display:flex;flex-direction:column;height:100%;overflow:hidden;background:#F4F5F8}.OperationsDetail.step1>section{transform:translateX(-33.33%)}.OperationsDetail.step2>section{transform:translateX(-66.66%)}.OperationsDetail>section{transform:translateX(0%);flex:1;width:300%;display:flex;transition:0.6s cubic-bezier(0.49, 0.01, 0.13, 1)}.OperationsDetail>section aside{flex:1}.OperationsDetail .done{background:#30a048;border:none;color:#fff;padding:15px;cursor:pointer}.OperationsDetail .done:hover{opacity:.9}
