.QBSelect{position:relative;background:#fff;border-radius:4px;border:1px solid rgba(0,0,0,0.3);display:inline-flex;justify-content:space-between;align-items:center;padding:7px 10px 7px 20px;box-sizing:border-box;max-width:100%;cursor:pointer;transition:.3s ease}.QBSelect:hover{background:rgba(0,0,0,0.15)}.QBSelect .label{font-size:1.2em;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;max-width:95%;display:inline-block}.QBSelect .hi{font-size:1.3em;margin-left:20px}.QBSelect.top ul{top:inherit;bottom:50px}.QBSelect ul{position:absolute;margin:0;padding:7px 0;list-style:none;top:40px;left:0;background:#fff;border:1px solid #eee;z-index:1;border-radius:4px;box-shadow:0 10px 20px rgba(0,0,0,0.05)}.QBSelect ul li{padding:7px 20px;white-space:nowrap}.QBSelect ul li:hover{color:#30a048}
