.Modal{position:fixed;top:0;left:0;width:100%;height:100vh;background:rgba(0,0,0,0.7);z-index:-1;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:0 20px;box-sizing:border-box;transition:opacity .5s ease;opacity:0}.Modal.open{opacity:1}.Modal.gone{z-index:6}.Modal>main{background:#fff;border-radius:5px;padding:40px;box-sizing:border-box;box-shadow:0 20px 40px rgba(0,0,0,0.5);width:50vw}
