.ReviewEmployee{cursor:pointer;display:flex;justify-content:space-between;align-items:center;margin-top:15px;border:1px solid #eeeff2;background:#fff;padding:20px}.ReviewEmployee:hover{border:1px solid #30a048}.ReviewEmployee .EmployeeName{flex:1}.ReviewEmployee:first-child{margin-top:0}.ReviewEmployee label:not(.Switch){opacity:.6;font-size:.8em}.ReviewEmployee .calculate{margin-right:20px}.ReviewEmployee .calculate article{align-items:flex-end}.ReviewEmployee .calculate article strong{font-family:'Bolt_demi'}.ReviewEmployee .calculate article:nth-of-type(2){align-items:flex-start;text-align:right;margin:0 40px}.ReviewEmployee .controls{flex:1;display:flex;align-items:center}.ReviewEmployee .controls>section{flex:1;display:flex;justify-content:flex-end}.ReviewEmployee .controls>section>div{display:flex;align-items:center}.ReviewEmployee .controls>section>div article{display:flex;flex-direction:column}
