.Sidenav{height:calc(100vh - 50px);position:fixed;top:50px;left:0;z-index:1;background:#393a3d;color:#fff}.Sidenav .btnCtn{display:flex;padding:20px}.Sidenav .btnCtn .btn{flex:1}.Sidenav .btnCtn .btn:after{display:none}.Sidenav .btnCtn .btn span{transform:translateX(7px);color:#fff;position:relative;display:inline-block}.Sidenav .btnCtn .btn span:after,.Sidenav .btnCtn .btn span:before{content:"";display:block;height:15px;width:1.5px;border-radius:4px;background:#fff;position:absolute;left:-12px;top:1px}.Sidenav .btnCtn .btn span:before{transform:rotate(90deg)}.SidenavList{padding:0;margin:0;width:220px;cursor:pointer}.SidenavList>li{position:relative;display:flex;align-items:center;justify-content:space-between}.SidenavList>li .hi{transform:translateX(-20px);opacity:.6;font-size:.7em}.SidenavList>li>a{color:#fff}.SidenavList a{color:#333;display:block;padding:15px 40px;text-decoration:none}.SidenavList>li{position:relative;min-width:180px;box-sizing:border-box;background:rgba(200,200,200,0);transition:.2s ease;font-size:15px}.SidenavList>li.current{border-left:6px solid #2ca01c}.SidenavList>li.current span{font-family:'Bolt_demi'}.SidenavList>li.current,.SidenavList>li:hover{background:rgba(200,200,200,0.1)}.SidenavList>li:hover ul{display:block}.SidenavList>li ul{display:none;padding:0;position:absolute;list-style:none;top:-6px;right:-215px;border:1px solid #d4d7dc;border-radius:3px;width:100%;z-index:5;background:#fff}.SidenavList>li ul:after{content:"";display:block;width:10px;height:10px;border:1px solid #d4d7dc;border-right:none;border-top:none;background:#fff;border-radius:3px;position:absolute;top:22px;left:-6px;transform:rotate(45deg)}.SidenavList>li ul li{position:relative}.SidenavList>li ul li:not(:first-child):after{content:"";display:block;height:1px;width:90%;right:0;top:0;background:#d4d7dc;position:absolute}.SidenavList>li ul li a,.SidenavList>li ul li span{display:block;padding:15px 20px}
