.Shorts{position:fixed;top:0;left:0;width:100%;height:100vh;background:rgba(0,0,0,0.7);z-index:4;display:flex;flex-direction:column;align-items:center;padding:20vh 20px 20vh;box-sizing:border-box}.Shorts main{background:#fff;border-radius:5px;padding:40px;width:380px;box-sizing:border-box;box-shadow:0 20px 40px rgba(0,0,0,0.5);position:relative;display:flex;flex-direction:column;justify-content:space-between}.Shorts main .close{position:absolute;right:30px;top:30px;border:none;cursor:pointer}.Shorts section{flex:1;display:flex;justify-content:center;background:rgba(0,0,0,0.8);box-shadow:inset 0 5px 5px rgba(0,0,0,0.5);box-sizing:border-box;padding:1px;margin-top:10px}
