.FPS{height:100%;box-sizing:border-box;background:#F4F5F8}.FPS .fpsImg{padding:20px}.FPS .FPSHeader+main>section:nth-of-type(2){height:calc(100vh - 265px);overflow-y:scroll}.FPS .Tabs{padding:0 20px;background:#393A3D;color:#fff}.FPS .Tabs #tabList>li:after{content:"|";opacity:.5;font-weight:300;font-family:'Bolt_th';display:inline-block;transform:translateX(30px)}.FPS .Tabs #tabList>li:last-child:after{display:none}.FPS .done{background:#30a048;border:none;color:#fff;padding:15px;cursor:pointer;width:100%;box-sizing:border-box}.FPS .done:hover{opacity:.9}
