.Breakdown{background:#fff;padding:0 40px;border-radius:4px;margin-bottom:0;transition:0.5s cubic-bezier(0.94, -0.01, 0.25, 1);max-height:0;overflow:hidden;box-shadow:0 5px 20px rgba(0,0,0,0);opacity:0}.Breakdown.breakdown{opacity:1;box-shadow:0 5px 20px rgba(0,0,0,0.07);padding:40px;margin-bottom:20px;max-height:600px}.Breakdown article{display:flex;justify-content:space-between;padding:5px 0 5px 20px}.Breakdown article:nth-of-type(1) span{font-size:1.2em;font-family:'Bolt_bold'}.Breakdown article h3{margin:0;color:#007f7c;position:relative}.Breakdown article h3:before{content:"";display:block;width:3px;height:17px;border-radius:5px;background:#007f7c;opacity:.9;position:absolute;top:0;left:-15px}.Breakdown .fullBreakdown{border-bottom:1px solid #ddd;padding:20px 0 10px 0}.Breakdown .rating{text-align:right;margin-top:10px}
