body{margin:0;font-family:'Bolt_rg',sans-serif;font-size:14px;color:#333}body.noOverflowmac,body.noOverflowpc{overflow:hidden}body.noOverflowpc{margin-right:17px}.qboWrapper.payrollPage{background:#F4F5F8}p{margin:0}input[type="search"],input[type="number"],input[type="text"],textarea,.dtDropdown{padding:8px;border:1px solid rgba(107,108,114,0.5);border-radius:4px;font-size:1.1em;font-weight:100;box-sizing:border-box;outline:1px solid rgba(48,160,72,0)}input[type="search"]:focus,input[type="number"]:focus,input[type="text"]:focus,textarea:focus,.dtDropdown:focus{border:1px solid #30a048}.visuallyHidden{position:absolute;overflow:hidden;clip:rect(0 0 0 0);height:1px;width:1px;margin:-1px;padding:0;border:0}h1{margin:0;font-family:'Bolt_bold';font-size:3.3em;line-height:1.2em}h2{font-family:'Bolt_bold';font-size:2em;margin:0}a.btn{text-decoration:none}.btn{background:#30a048;border:none;color:#fff;font-size:1.2em;padding:10px 25px;border-radius:50px;cursor:pointer;position:relative;overflow:hidden}.btn.tiny{padding:5px 15px;font-size:.9em}.btn.tiny.ghost{box-shadow:0 0 0 1px #666}.btn:active{transform:scale(0.992)}.btn.dark{background:#333;color:#fff}.btn.dark:after{background:#262626}.btn.dull_white{background:none;border:2px solid #fff}.btn.dull_white:hover{background:none;border:2px solid rgba(255,255,255,0.7)}.btn.dull_white:after{display:none}.btn.ghost{background:none;box-shadow:0 0 0 2px #666}.btn.ghost:after{background:#f2f2f2}.btn.ghost span,.btn.ghost a{color:#333}.btn.ghost.white{box-shadow:0 0 0 2px #fff;color:#fff}.btn a,.btn span{transition:.3s ease;color:#fff;text-decoration:none}.btn span{position:relative;z-index:1}.btn:after{content:"";display:block;position:absolute;top:0;left:0;width:0;height:100%;background:#2a8c3f;border-radius:50px;transition:0.2s cubic-bezier(0.9, 0.03, 0.15, 1.01);z-index:0}.btn:focus{outline:none}.btn:hover:after{width:100%}#bird{height:100vh;display:flex;flex-direction:column}.qboWrapper{display:flex;flex-direction:column;fl