.EmployeePayslip{display:flex;justify-content:space-between;align-items:center;background:#fff;padding:20px;margin-top:15px;border:1px solid #eeeff2}.EmployeePayslip label{opacity:.6;font-size:.8em}.EmployeePayslip .controls{flex:1;display:flex;align-items:center}.EmployeePayslip .controls .enableEdit{opacity:1;transition:.2s ease}.EmployeePayslip .controls .enableEdit.disabled{opacity:.4}
