.SelfAssessment{margin:0 auto;padding:30px 30px 80px;min-height:calc(100vh - 109px);box-sizing:border-box;width:70vw}.SelfAssessment .titleCtn{display:flex;justify-content:space-between;position:fixed;z-index:3;top:50px;right:0;width:calc(100% - 220px);background:#393a3d;box-sizing:border-box}.SelfAssessment .titleCtn h2{padding:10px 25px;color:#fff;font-size:1.1em;font-family:'Bolt_demi'}.SelfAssessment .SelfAssessmentTitle{margin:30px 0;display:flex;align-items:baseline;justify-content:space-between}.SelfAssessment .SelfAssessmentTitle h1{font-size:1.8em;font-family:'Bolt_demi'}.SelfAssessment .SATable{background:#fff;box-shadow:0 2px 3px rgba(0,0,0,0.15);border:1px solid rgba(200,200,200,0.2);border-radius:4px;padding:20px;margin-top:40px;overflow:hidden;transition:.4s ease}.SelfAssessment .SATable:nth-of-type(1){margin-top:0}.SelfAssessment .SATable h2{position:relative;display:inline-block;z-index:1;user-select:none;cursor:pointer}.SelfAssessment .SATable h2 .hi{transform:rotate(0deg);transition:.4s ease}.SelfAssessment .SATable.collapsed table{opacity:0}.SelfAssessment .SATable.collapsed h2 .hi{transform:rotate(-90deg)}.SelfAssessment .SATable table{opacity:1;transition:.5s ease;width:100%}.SelfAssessment .SATable table td:nth-of-type(2),.SelfAssessment .SATable table td:nth-of-type(4){text-transform:uppercase;opacity:.7;font-size:.85em}.SelfAssessment .SATable table td:nth-of-type(3),.SelfAssessment .SATable table td:nth-of-type(5){font-family:'Bolt_bold';font-size:1.1em}.SelfAssessment .SATable table th,.SelfAssessment .SATable table td{padding:5px 0;text-align:right}.SelfAssessment .SATable table th:first-child,.SelfAssessment .SATable table td:first-child{width:50%;text-align:left}.SelfAssessment .SATable table th{text-transform:uppercase;font-size:.9em;padding:10px 0}.SelfAssessment .SATable table tbody td:not(:first-child),.SelfAssessment .SATable table tfoot td:not(:first-child){width:12%}.SelfAssessment .SATable table tbody tr:last-child td{padding-bottom:20px}.SelfAssessment .SATable table tfoot tr td:first-child{text-transform:uppercase;font-size:1.2em;color:#000}.SelfAssessment .SATable table tfoot tr:first-child td{padding-top:20px;border-top:1px solid #ddd}
