.ReviewDrawer{display:flex;flex-direction:column;height:100%;overflow:hidden;background:#F4F5F8}.ReviewDrawer>section{flex:1;display:flex;transition:0.6s cubic-bezier(0.49, 0.01, 0.13, 1)}.ReviewDrawer>section aside{flex:1}.ReviewDrawer .done{background:#30a048;border:none;color:#fff;padding:15px;cursor:pointer}.ReviewDrawer .done:hover{opacity:.9}
