#tabList{display:flex;padding:0;list-style:none;position:relative;margin:0}#tabList li{padding:10px 30px;font-size:1.1em;cursor:pointer}#tabList li.active{font-family:'Bolt_demi'}.Tabs{border-bottom:2px solid rgba(212,215,220,0.38)}.Tabs .border{display:inline-block;height:4px;border-radius:4px;background:#30a048;transition:.4s ease;position:relative;top:5px}
