.Drawer{position:fixed;z-index:3;width:100vw;height:100vh;opacity:1;background:rgba(0,0,0,0);display:flex;justify-content:flex-end;transition:.8s ease}.Drawer.open{background:rgba(0,0,0,0.5)}.Drawer.open>section{transform:translateX(0)}.Drawer>section{transition:0.8s cubic-bezier(0.82, 0, 0.18, 1.01);transform:translateX(100%);background:#fff;width:565px;box-shadow:0 0 20px 0 rgba(0,0,0,0.5)}
