.trowser{background:rgba(20,20,20,0);position:fixed;width:100%;left:0;top:0;height:100vh;z-index:6;transition:0.6s cubic-bezier(0.79, 0.04, 0.25, 1);transform:translateY(-100vh)}.trowser.open{background:rgba(20,20,20,0.7);transform:translateY(0vh)}.trowser.open section.inner{box-shadow:0 0 40px 0px #000}.trowser section.inner{background:#fff;height:100%;margin:0 auto;box-shadow:0 0 40px 0px rgba(0,0,0,0);transition:0.6s cubic-bezier(0.79, 0.04, 0.25, 1)}
