.EstimatedIncome{background:#fff;position:sticky;top:0;z-index:7}.EstimatedIncome>article{display:inline-flex;flex-direction:column;box-shadow:0 4px 4px rgba(0,0,0,0.1);border:1px solid rgba(200,200,200,0.2);border-radius:4px;padding:20px 30px}.EstimatedIncome>article h2{font-size:1.1em;font-weight:300;font-family:'Bolt_bold';margin-bottom:10px}.EstimatedIncome>article strong{font-size:2.4em;font-family:'Bolt_bold'}.EstimatedIncome>article span{font-size:.85em;margin-top:6px}
