.qbLogo{display:flex;flex-direction:column;justify-content:center;padding:0 30px 0 20px;background:#393a3d;width:220px;box-sizing:border-box;cursor:pointer}.qbLogo img{height:25px}.topbarActions{display:flex}.topbarActions li{display:flex;align-items:center}.topbarActions li button{height:100%;padding:0 10px;display:flex;align-items:center;font-family:harmonyicons;background:rgba(0,0,0,0);border:none;transition:.3s ease;cursor:pointer}.topbarActions li button:hover{opacity:.7}.topbarActions li button i:after{font-style:normal;color:#6b6c73;font-size:30px}.topbarActions li button[data-label="create"] i:after{content:"\F085"}.topbarActions li button[data-label="search"] i:after{content:"\F042"}.topbarActions li button[data-label="settings"] i:after{content:"\F044"}.topbarActions li button[data-label="help"] i:after{content:"\F054"}.topbarActions li button svg{width:25px}.topbarActions li button svg path{fill:#6b6c73}.Topbar{display:flex;justify-content:space-between;height:48px;align-items:center;background:#393A3D;background:#fff;min-height:50px;position:sticky;top:0;z-index:2}.Topbar button span{font-family:'Bolt_rg';color:#6b6c73;padding:0 7px;font-size:14px}.rtpbr,.ltpbr{display:flex;height:100%}.menuButton{border:none;display:flex;align-items:center;justify-content:center;padding:10px 25px;text-align:center;cursor:pointer}.menuButton .hi-menu{color:#6b6c73;font-size:2.3em}
