.progressBar{position:relative;opacity:0;transition:opacity .4s ease;margin-bottom:20px}.progressBar section{position:relative;overflow:hidden;border:1px solid #30a048;border-radius:20px;height:10px}.progressBar.show{opacity:1}.progressBar .stepOf{position:absolute;top:-20px;right:0}.progressBar .progress{position:absolute;left:0;top:0;height:10px;border-radius:20px 0 0 20px;transition:width .6s cubic-bezier(0.6, 0.01, 0.07, 1.01);background:#30a048}
