.Pile{position:fixed;width:calc(100% - 220px);top:0;left:220px;z-index:2;display:flex;justify-content:center}.Pile.show article{transform:translateY(60px)}.Pile article{background:rgba(48,160,72,0.1);color:#30a048;border:1px solid rgba(48,160,72,0.4);border-radius:4px;padding:15px 40px;transform:translateY(-100%);transition:0.8s cubic-bezier(0.96, -0.01, 0.21, 0.99);display:flex;align-items:center}.Pile article .hi{font-size:1.5em;margin-right:6px}
