.file-drop{position:relative;height:100px;width:100%;margin:20px 0;cursor:pointer}.file-drop .file-drop-target{position:absolute;top:0;left:0;height:100%;width:100%;border-radius:2px;display:flex;flex-direction:column;align-items:center;justify-content:center;align-content:center;text-align:center;background:#f4f5f8;border:2px dashed rgba(183,187,196,0.7);transition:.3s ease}.file-drop .file-drop-target.file-drop-dragging-over-target{background:rgba(48,160,72,0.1);border:2px dashed rgba(48,160,72,0.7)}
